// Contact.js

import React from 'react';
import { useParams } from 'react-router-dom';
import NavbarBs from '../navbar';

const Contact = () => {
    const params = useParams();
    var pageNo = params.pageNo; 
    var message = ``;
    if (parseInt(pageNo) > 5) {
        // return <Redirect to="/" />
        message = `Page number outside accepted range`
        return (
            <div>
                <h3>{message}</h3>
            </div>
        )
    }
    else {
        return ( 
            <div>
                <NavbarBs/>
                <div>
                    <h2>Welcome to "Contact" page (page# = {pageNo})</h2>
                </div>
            </div>
        )
    }
}

export default Contact;
